textarea {
  height: 60vh !important;
}
.alert {
  z-index: 9999;
  position: fixed;
  top: -50px;
  left: 0;
  right: 0;
  /* opacity: 0; */
  animation: shows 300ms 1 linear, hides 200ms 1.5s linear;

  animation-fill-mode: forwards;
}
@keyframes shows {
  100% {
    top: 0px;
  }
}
@keyframes hides {
  100% {
    top: -50px;
  }
}
.alert_holder {
  position: relative;
  height: 50px;
}
button {
  min-width: 100px !important;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background-color: #051d24;
}
.btn_con {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.checkbox {
  opacity: 0;
  position: absolute;
}

.label {
  width: 40px;
  height: 20px;
  background-color: #111;
  display: flex;
  border-radius: 50px;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  transform: scale(1.5);
}

.ball {
  width: 18px;
  height: 18px;
  background-color: white;
  position: absolute;
  top: 1px;
  left: 1px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

/*  target the elemenent after the label*/
.checkbox:checked + .label .ball {
  transform: translateX(20px);
}

.fa-moon {
  font-size: 12px;
  color: pink !important;
}

.fa-sun {
  font-size: 12px;

  color: yellow !important;
}
.pallet_circle {
  min-width: 30px !important;
  height: 30px;
  border-radius: 100px;
  margin-left: 3px;
  border: none;
}
#light {
  background-color: antiquewhite;
}
#blue {
  background-color: blue;
}
#dark {
  background-color: #333;
}
#golden {
  background-color: #ff4b4b;
}

.tools-links {
  list-style: none;
}
.tools-links li {
  padding: 10px;
  border: 1px solid rgb(255, 255, 255);
  max-width: 300px;
  box-shadow: 0px 0px 10px 1px rgb(0, 0, 0);
  border-radius: 100px;
  transition: 500ms;
}
.tools-links li:hover {
  box-shadow: 0px 0px 10px 1px rgb(255, 255, 255);
}

.tools-links li a {
  text-decoration: none;
}
